.app-wrapper {
    display: grid;
    max-width: 1600px;
    min-height: 100vh;
    margin: 0 auto;
    grid-template-areas:
            'h h'
            'n c';
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
}

.app-wrapper-content {
    grid-area: c;
    background-color: #edeef0;
    padding: 30px;
}








